<template>
  <div class="list">
    <div class="listContent">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        :immediate-check="false"
        :offset="1"
        @load="onLoad"
      >
        <div style="display: flex">
          <div class="contentLeft">
            <div
              class="listContentItem"
              v-for="(item, index) in ContentData1"
              :key="index"
            >
              <div class="listContentTop" @click="routerTo('detail', item)">
                <img class="TopImg" :src="setTopImage(item.fileList)" alt />
                <div class="TopFont">
                  {{ item.enrollEd || setEnroll(item.enroll) }}
                </div>
                <div class="TopText">
                  <img src="@/assets/phone/APP_activity/position.png" alt />
                  <div>{{ item.placeName }}</div>
                </div>
              </div>
              <div class="listContentBom">
                <div class="fabulous">
                  <div>{{ subTime(item.startTime) }}</div>
                  <div @click="thumbsUp(item.uuid)">
                    <img :src="setThumbImage(item.uuid)" alt />
                    <div>{{ item.thumbsNum }}</div>
                  </div>
                </div>
                <div>{{ item.activityName }}</div>
                <div>{{ item.activityTheme }}</div>
              </div>
            </div>
          </div>
          <div class="contentRight">
            <div class="AllText" @click="popupShow = !popupShow">
              {{ activityLabel }} / {{ timeType }}
            </div>
            <div
              class="listContentItem"
              v-for="(item, index) in ContentData2"
              :key="index"
            >
              <div class="listContentTop" @click="routerTo('detail', item)">
                <img class="TopImg" :src="setTopImage(item.fileList)" alt />
                <div class="TopFont">
                  {{ item.enrollEd || setEnroll(item.enroll) }}
                </div>
                <div class="TopText">
                  <img src="@/assets/phone/APP_activity/position.png" alt />
                  <div>{{ item.placeName }}</div>
                </div>
              </div>
              <div class="listContentBom">
                <div class="fabulous">
                  <div>{{ subTime(item.startTime) }}</div>
                  <div @click="thumbsUp(item.uuid)">
                    <img :src="setThumbImage(item.uuid)" alt />
                    <div>{{ item.thumbsNum }}</div>
                  </div>
                </div>
                <div>{{ item.activityName }}</div>
                <div>{{ item.activityTheme }}</div>
              </div>
            </div>
          </div>
        </div>
      </van-list>
    </div>

    <van-popup
      v-model="popupShow"
      position="top"
      :style="{ height: '60%', width: '100%' }"
    >
      <search
        :mySearchObj="searchObj"
        :myTimeType="timeType"
        :myActivityLabel="activityLabel"
        @submit="trySearch"
        @canel="canelSearch"
      ></search>
    </van-popup>
  </div>
</template>

<script>
import { activityList } from "@/api/case/board";
import {
  getActivityList,
  listUserLikes,
  addUserLikeredis,
  deleteUserLike,
} from "@/api/data/reservation";
import search from "./search.vue";
import { List } from "vant";
import { getActivityPeopleNumber } from "@/api/data/reservation";
export default {
  components: {
    search,
    "van-list": List,
  },
  data() {
    return {
      loading: false,
      finished: false,
      ContentData1: [],
      ContentData2: [],
      extList: ["png", "jpg", "jpeg", "gif", "bmp"],
      thumbsList: [], //用户点赞列表
      isEnrollList: [],
      popupShow: false,
      yearNow: new Date().getFullYear(),
      searchObj: {
        activityTypes: "",
        startTime: "2022-01-01",
        endTime: "2022-12-31",
      },
      pageObj: {
        pageNum: 1,
        pageSize: 10,
      },
      timeType: "本年",
      activityLabel: "全部",
    };
  },
  methods: {
    onLoad() {
      this.pageObj.pageNum += 1;
      this.getList(false);
    },
    routerTo(str, item) {
      this.$store.commit("SET_ACTIVITY", item);
      this.$router.push({ name: str, params: { item } });
    },
    thumbsUp(id) {
      let _this = this;
      if (this.thumbsList.findIndex((c) => c.activityId == id) == -1) {
        //点赞
        addUserLikeredis(id).then(async function (res) {
          _this.getThumbs();
          _this.resetThumbsNum(true, id);
        });
      } else {
        //取消
        deleteUserLike(id).then(async function (res) {
          _this.getThumbs(false, id);
          _this.resetThumbsNum(false, id);
        });
      }
    },
    resetThumbsNum(flag, id) {
      if (flag) {
        this.ContentData1.findIndex((c) => c.uuid == id) != -1
          ? (this.ContentData1.find((c) => c.uuid == id).thumbsNum += 1)
          : "";
        this.ContentData2.findIndex((c) => c.uuid == id) != -1
          ? (this.ContentData2.find((c) => c.uuid == id).thumbsNum += 1)
          : "";
      } else {
        this.ContentData1.findIndex((c) => c.uuid == id) != -1
          ? (this.ContentData1.find((c) => c.uuid == id).thumbsNum -= 1)
          : "";
        this.ContentData2.findIndex((c) => c.uuid == id) != -1
          ? (this.ContentData2.find((c) => c.uuid == id).thumbsNum -= 1)
          : "";
      }
    },
    setThumbImage(id) {
      if (this.thumbsList.findIndex((c) => c.activityId == id) == -1) {
        return require("@/assets/phone/APP_activity/thumbs-down.png");
      } else {
        return require("@/assets/phone/APP_activity/thumbs-up.png");
      }
    },
    subTime(val) {
      if (val) {
        let _startTime = new Date(val);
        return (
          _startTime.getFullYear() +
          "-" +
          ((_startTime.getMonth() < 9 ? "0" : "") +
            (_startTime.getMonth() + 1)) +
          "-" +
          ((_startTime.getDate() < 10 ? "0" : "") + _startTime.getDate())
        );
      } else {
        return val;
      }
    },
    setTopImage(fileList) {
      let _this = this;
      let _file = fileList.find(
        (c) => _this.extList.findIndex((a) => a == c.fileType) != -1
      );
      if (_file) {
        return process.env.VUE_APP_ACCESSORY_URL + _file.filePath;
      } else {
        return require("@/assets/phone/APP_activity/bg1.png");
      }
    },
    setEnroll(val) {
      if (this.isEnrollList.find((c) => c.dictValue == val).dictLabel == "是") {
        return "可报名";
      } else {
        return "";
      }
    },
    trySearch: async function (val) {
      this.getThumbs();
      this.searchObj = val.searchObj;
      this.pageObj.pageNum = 1;
      this.finished = false;
      this.timeType = val.timeType;
      this.activityLabel = val.activityLabel;
      this.popupShow = false;
      this.getList(true);
    },
    canelSearch(val) {
      this.popupShow = false;
    },
    getList(flag) {
      let _this = this;
      let _searchObj = Object.assign({}, this.searchObj, this.pageObj);
      if (flag) {
        this.ContentData1 = [];
        this.ContentData2 = [];
      }

      activityList(_searchObj).then((res) => {
        getActivityList().then((reserRes) => {
          console.log(res);
          res.rows.map((item, index) => {
            if (
              item.remainingPlaces == "0" &&
              _this.isEnrollList.find((c) => c.dictValue == item.enroll)
                .dictLabel == "是"
            ) {
              item.enrollEd = "报名已满";
            } else if (
              reserRes.data.findIndex((c) => c.activityId == item.uuid) != -1
            ) {
              item.enrollEd = "已报名";
            } else {
              item.enrollEd = "";
            }

            if (index % 2) {
              this.ContentData2.push(item);
            } else {
              this.ContentData1.push(item);
            }
          });
          _this.loading = false;
          if (res.rows.length < _searchObj.pageSize) {
            _this.finished = true;
          }
        });
      });
    },
    getThumbs() {
      listUserLikes().then((res) => {
        this.thumbsList = res.data;
      });
    },
  },
  created: async function () {
    await this.dictType("is_electronic").then((res) => {
      this.isEnrollList = res;
    });
    this.getThumbs();
  },
  mounted: async function () {
    this.searchObj.startTime = this.yearNow + "-01-01";
    this.searchObj.endTime = this.yearNow + "-12-31";
    this.getList(true);
  },
};
</script>

<style lang="scss" scoped>
.list {
  width: 100vw;
  font-size: 3vw;
  background-color: #f9f9f9;
  .listContent {
    width: 98vw;
    margin: 0 auto;
    // height: 66.5vh;
    display: flex;
    overflow-y: hidden;
    .contentRight,
    .contentLeft {
      width: 50%;
      position: relative;

      .listContentItem {
        width: 100%;
        height: 34.65vh;
        margin-top: 1.5vw;
        float: left;
        border-radius: 2vw;
        padding: 1vw;
        box-sizing: border-box;
        background-color: #fff;
        color: #434343;
        font-size: 3.4vw;
        font-weight: 500;
        .listContentTop {
          width: 100%;
          height: 72.85%;
          position: relative;
          .TopImg {
            width: 100%;
            height: 100%;
          }
          .TopFont {
            padding: 1vw;
            color: rgba(230, 230, 230, 0.938);
            background-color: rgba(82, 81, 81, 0.603);
            border-radius: 1vw;
            position: absolute;
            top: 0;
            right: 0;
          }
          .TopText {
            width: 100%;
            display: flex;
            position: absolute;
            bottom: 3vw;
            justify-content: left;
            align-items: center;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            img {
              width: 3.5vw;
              height: 3.5vw;
              margin-left: 2vw;
            }
            div {
              font-size: 3.2vw;
              color: white;
              margin-left: 3vw;
            }
          }
        }
        .listContentBom {
          // list-style: none;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          margin-top: 1.43%;
          padding: 1vw;

          .fabulous {
            height: 3vh;
            line-height: 3vh;
            display: flex;
            justify-content: space-between;
            margin-bottom: 1vw;
            div:nth-child(2) {
              display: flex;
              img {
                width: 4vw;
                height: 4vw;
                margin-top: 0.4vw;
              }
            }
          }
        }
      }
      .AllText {
        margin-top: 2.5vw;
        width: 100%;
        text-align: center;
        height: 5.8vh;
        color: white;
        line-height: 5.8vh;
        border-radius: 1vw;
        background-color: #3fc18d;
        font-size: 4vw;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .contentRight {
      display: inline-block;
    }
  }
}
.van-list {
  width: 100%;
}
</style>
