<template>
  <div class="caseIndex">
    <span class="optionBtn"> {{ activityLabel }}/ {{ timeType }}</span>
    <div class="optionBox">
      <div class="optionHead">
        {{ activityLabel }}/
        {{ timeType }}
        <span class="el-icon-caret-bottom"></span>
      </div>
      <div class="clearBox">
        <div class="clearBtn" @click="clearOption">清空</div>
      </div>
      <div class="optionTitle">活动类型</div>
      <div class="options activityType">
        <el-checkbox-group v-model="checkList">
          <el-checkbox
            v-for="(item, index) in actionTypeList"
            :key="index"
            :label="item.dictValue"
            >{{ item.dictLabel }}</el-checkbox
          >
        </el-checkbox-group>
      </div>
      <div style="clear: both"></div>
      <div class="optionTitle">时间选择</div>
      <div class="options">
        <div
          :class="['optionItem', { active: timeType == '本年' }]"
          @click="timeClick('本年')"
        >
          本年
        </div>
        <div
          :class="['optionItem', { active: timeType == '本月' }]"
          @click="timeClick('本月')"
        >
          本月
        </div>
      </div>
      <div class="options">
        <div
          :class="['optionItem', { active: timeType == '上年' }]"
          @click="timeClick('上年')"
        >
          上年
        </div>
        <div
          :class="['optionItem', { active: timeType == '上月' }]"
          @click="timeClick('上月')"
        >
          上月
        </div>
        <div
          :class="['optionItem', { active: timeType == '更多' }]"
          @click="timeClick('更多')"
        >
          更多
        </div>
      </div>

      <div v-if="timeType == '更多'">
        <van-field
          v-model="searchObj.startTime"
          is-link
          readonly
          name="startTimePicker"
          label="开始时间"
          placeholder="选择开始时间"
          @click="(AddDateFlag = true), (startTimeFlag = 'start')"
        />
        <van-field
          v-model="searchObj.endTime"
          is-link
          readonly
          name="endTimePicker"
          label="结束时间"
          placeholder="选择结束时间"
          @click="(AddDateFlag = true), (startTimeFlag = 'end')"
        />
      </div>

      <div class="optionSaveBox">
        <div class="optionSaveBtn" @click="optionSure">确定</div>
        <div class="optionSaveBtn" @click="optionCancel">取消</div>
      </div>
    </div>
    <van-popup
      v-model="AddDateFlag"
      position="bottom"
      teleport="#app"
      :style="{ height: '40%' }"
    >
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title="选择完整时间"
        @confirm="onConfirm"
        @cancel="onCancel"
        :minDate="minDate"
      />
    </van-popup>
  </div>
</template>

<script>
import { ref } from "vue";
import { userParkDeptList } from "@/api/user";
export default {
  props: ["mySearchObj", "myTimeType", "myActivityLabel"],
  data() {
    return {
      stationList: [],
      yearNow: new Date().getFullYear(),
      yearPrev: new Date().getFullYear() - 1,
      monthNow: new Date().getMonth() + 1,
      monthPrev: new Date().getMonth(),
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      minDate: new Date(1990, 0, 1),
      currentDate: new Date(),
      AddDateFlag: false,
      startTimeFlag: "",
      actionTypeList: [], //活动类型list
      timeType: "",
      searchObj: {},
      checkList: [],
      activityLabel: "",
      searchObjOld: {},
      checkListOld: [],
      timeTypeOld: "",
      activityLabelOld: "",
    };
  },
  watch: {
    myTimeType(val) {
      this.timeType = val;
    },
    startTimeFlag(val) {
      if (val == "start") {
        this.currentDate = new Date(this.searchObj.startTime);
      } else {
        this.currentDate = new Date(this.searchObj.endTime);
      }
    },
    checkList(val) {
      this.searchObj.activityTypes = val.join(",");
      this.activityDict(val);
    },
  },
  methods: {
    clearOption() {
      this.timeClick("本年");
      this.checkList = [];
    },
    activityDict(val) {
      if (val.length == this.actionTypeList.length || val.length == 0) {
        this.activityLabel = "全部";
      } else if (val.length == 1) {
        let label = this.actionTypeList.find((item) => {
          return item.dictValue == val[0];
        });
        if (label) {
          this.activityLabel = label.dictLabel;
        } else {
          this.activityLabel = "";
        }
      } else {
        this.activityLabel = "更多";
      }
    },
    getMonthLastDay(year, month) {
      let new_year = year; //取当前的年份
      let new_month = month++; //取下一个月的第一天，方便计算（最后一天不固定）
      if (month > 12) {
        new_month -= 12; //月份减
        new_year++; //年份增
      }
      var new_date = new Date(new_year, new_month, 1); //取当年当月中的第一天
      return (
        year +
        "-" +
        (month - 1 < 10 ? "0" + (month - 1) : month - 1) +
        "-" +
        new Date(new_date.getTime() - 1000 * 60 * 60 * 24).getDate()
      );
    },
    timeClick(str) {
      this.timeType = str;
      if (str == "本年") {
        this.searchObj.startTime = this.yearNow + "-01-01";
        this.searchObj.endTime = this.yearNow + "-12-31";
      } else if (str == "本月") {
        this.searchObj.startTime =
          this.yearNow +
          "-" +
          (this.monthNow < 10 ? "0" + this.monthNow : this.monthNow) +
          "-01";
        this.searchObj.endTime = this.getMonthLastDay(
          this.yearNow,
          this.monthNow
        );
      } else if (str == "上年") {
        this.searchObj.startTime = this.yearPrev + "-01-01";
        this.searchObj.endTime = this.yearPrev + "-12-31";
      } else if (str == "上月") {
        if (this.monthPrev == 0) {
          this.searchObj.startTime = this.yearPrev + "-12-01";
          this.searchObj.endTime = this.yearPrev + "-12-31";
        } else {
          this.searchObj.startTime =
            this.yearNow +
            "-" +
            ((this.monthPrev < 10 ? "0" : "") + this.monthPrev) +
            "-01";
          this.searchObj.endTime = this.getMonthLastDay(
            this.yearNow,
            this.monthPrev
          );
        }
      }
    },
    optionSure() {
      this.setHtml();
      this.$emit("submit", {
        searchObj: this.searchObj,
        timeType: this.timeType,
        activityLabel: this.activityLabel,
      });
    },
    optionCancel() {
      this.searchObj = this.$deepCopy(this.searchObjOld);
      this.checkList = this.searchObjOld.activityTypes
        ? this.searchObjOld.activityTypes.split(",")
        : [];
      this.timeType = this.$deepCopy(this.timeTypeOld);
      console.log(this.activityLabel);
      this.$emit("canel", {
        searchObj: this.searchObj,
        timeType: this.timeType,
        activityLabel: this.activityLabel,
      });
    },
    onConfirm(val) {
      if (this.startTimeFlag == "start") {
        this.searchObj.startTime = this.getDate(val);
      } else {
        this.searchObj.endTime = this.getDate(val);
      }
      this.AddDateFlag = false;
    },
    onCancel() {
      this.AddDateFlag = false;
    },
    getDate(val) {
      let _date = new Date(val);
      return (
        _date.getFullYear() +
        "-" +
        ((_date.getMonth() < 9 ? "0" : "") + (_date.getMonth() + 1)) +
        "-" +
        ((_date.getDate() < 10 ? "0" : "") + _date.getDate())
      );
    },
    setHtml() {
      this.searchObjOld = this.$deepCopy(this.searchObj);
      this.checkListOld = this.searchObj.activityTypes
        ? this.searchObj.activityTypes.split(",")
        : [];
      this.timeTypeOld = this.$deepCopy(this.timeType);
      this.activityLabelOld = this.$deepCopy(this.activityLabel);
    },
  },
  created: async function () {
    await this.dictType("science_activities_type").then((res) => {
      this.actionTypeList = res;
    });
  },
  mounted: async function () {
    this.timeType = this.$deepCopy(this.myTimeType);
    this.searchObj = this.$deepCopy(this.mySearchObj);
    this.checkList = this.mySearchObj.activityTypes
      ? this.mySearchObj.activityTypes.split(",")
      : [];
    this.timeClick(this.timeType);
    this.setHtml();
  },
};
</script>

<style lang="scss" scoped>
.caseIndex {
  width: 100%;
  max-width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  display: flex;

  .optionBtn {
    font-size: 1.04vh;
    font-weight: 400;
    color: #535353;
    cursor: pointer;
    position: absolute;
    top: 1.04vh;
    left: 1.85vw;
    z-index: 9;
  }
  .caseContainerBox {
    width: 100%;
    flex: 1;
  }
  .optionBox {
    position: absolute;
    top: -0.52vh;
    right: 0;
    width: calc(100% - 2.08vw);
    height: 100%;
    z-index: 9;
    border-left: 1px solid #8fbed4;
    background: #fff;
    padding: 0 1.04vw;
    transition: all 0.4s;
    box-shadow: 2px -2px 5px rgba(0, 0, 0, 0.5);
    .optionHead {
      margin-top: 0.52vh;
      height: 5.92vh;
      line-height: 5.92vh;
      font-size: 1.3vh;
      font-weight: 400;
      color: #535353;
      text-align: left;
    }
    .clearBox {
      width: 100%;
      text-align: left;
      .clearBtn {
        display: inline-block;
        opacity: 0.85;
        font-size: 1.67vh;
        color: #218bbd;
        cursor: pointer;
      }
    }
    .optionTitle {
      height: 3.43vh;
      font-size: 2.41vh;
      text-align: left;
      color: #535353;
      line-height: 3.43vh;
      margin-top: 1.65vh;
    }
    .activityType {
      height: 22%;
      overflow-y: auto;
      &::-webkit-scrollbar {
        display: none;
      }
    }
    .options {
      float: left;
      margin-bottom: 12px;
      .optionItem {
        display: inline-block;
        width: 14.62vw;
        height: 3.96vh;
        background: #f8fbfb;
        border-radius: 2px;
        line-height: 3.96vh;
        text-align: center;
        font-size: 1.67vh;
        color: #686868;
        margin-left: 0.78vw;
        cursor: pointer;
        &:hover {
          background: #d3f4ff;
        }
      }
      .optionItem.active {
        background: #d3f4ff;
      }
    }
    .optionSaveBox {
      position: absolute;
      bottom: 2.31vh;
      left: 0;
      width: 100%;
      height: 3.59vh;
      line-height: 3.59vh;
      text-align: center;
      color: #218bbd;
      font-size: 1.3vh;
      .optionSaveBtn {
        width: 14.73vw;
        background: #f8fbfb;
        margin: 0 0.36vw;
        display: inline-block;
        cursor: pointer;
      }
    }
  }
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 100% !important;
}

.el-checkbox {
  width: calc(50% - 30px);
}
</style>
